.btn {
  padding: 15px 50px;
  height: 48px;
  text-align: center;
  background-color: $color-primary;
  border-radius: 24px;
  border: none;
  font: 700 16px $font-primary;
  transition: 0.3s all ease-out;
  color: #fff;
  &:focus {
    outline: none;
  }
  &:hover {
    color: #4a4a4a;
  }
}